header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 30px;
}

.logo {
    margin: 10px 0 10px 40px;
    height: 40px;
}

.logo > img  {
    height: 100%;
}

.nav {
    display: flex;
    justify-content: flex-end;
}

.nav > a {
    margin-right: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    text-decoration: none;
    color: white;
    font-size: 30px;
}

.nav > a:hover {
    color: #1db954;
}

.mnav {
    display: none;
    position: relative;
    margin-right: 20px;
    border-bottom: 17px double #1db954; 
    border-top: 6px solid #1db954; 
    content:"";
    height: 5px; 
    width:30px;
}

.mnav:hover .menu {
    display: flex;
}

.menu {
    position: absolute;
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    left: -170px;
    top: 30px;
    background-color: #1db954;
    border-radius: 10px;
    height: 200px;
    width: 200px;
    z-index: 1;
}

.menu > a {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    text-decoration: none;
    color: white;
    font-size: 30px;
}

@media screen and (max-width: 375px) {

    .logo {
        margin-left: 20px;
    }

    .nav {
        display: none;
    }

    .mnav {
        display: block;
    }
}