.album_page {
    width: 1000px;
}

.about {
    display: flex;
    height: 200px;
    width: 100%;
    margin-bottom: 30px;
}

.about > img {
    height: 200px;
    width: 200px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.info {
    height: 100%;
    width: 800px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #292929;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.type {
    margin: 10px;
}

.type > p {
    font-weight: bold;
    color: white;
}

.name {
    margin: 10px;
}

.name > a {
    font-size: 40px;
    color: white;
    text-decoration: none;
}

.name > a:hover {
    color: #1db954;
}

.artists {
    margin: 10px;
}

.artists > a {
    margin-right: 5px;
    color: white;
    text-decoration: none;
}

.artists > a:hover {
    color: #1db954;
}

.tracks {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.track {
    margin: 10px;
}

@media screen and (max-width: 375px) {

    .album_page {
        width: 200px;
    }

    .type, .name, .artists {
        display: none;
    }
}